import React from "react";
import {
  Jumbotron
} from "reactstrap";
import BootstrapTable from "react-bootstrap-table-next";

export class User extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loggedIn: true,
      items: []
    };
    }  


  componentDidMount() {
    fetch('../../../server/routes/items.js', {
      headers : { 
        'Content-Type': 'application/json',
        'Accept': 'application/json'
       }

    }) 
      .then(res => res.json())
      .then(users => this.setState({ items }));
  }

  render() {
    let columns = [
      {
        dataField: "id",
        text: "User ID"
      },
      {
        dataField: "name",
        text: "User Name",
      },
      {
        dataField: "type",
        text: "User Type"
      }
    ];

    return (
      <div>
        <Jumbotron>
          <h1 className="display-4">Users</h1>
          <br/>
          {/* <BootstrapTable
            keyField="id"
            data={users}
            columns={columns}
            striped
            hover
            condensed
          /> */}
        </Jumbotron>
        <h1>Users</h1>
        {this.state.items.map(user =>
          <div key={items.id}>{items.name}</div>
        )}
      </div>
    );
  }
}



